<template>
  <div id="vue-app">
    <v-toolbar flat color="white">
      <v-toolbar-title class="grey--text">Member List</v-toolbar-title>
      <v-divider class="mx-2" inset vertical></v-divider>
      <v-spacer></v-spacer>
      <template v-slot:extension></template>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn class="btn" v-on="on">
            <span aria-hidden="true" class="btnLogo">
              <svg width="36" height="36" viewBox="0 0 36 36">
                <path fill="#FF0000" d="M16 16v14h4V20z" />
                <path fill="#000080" d="M30 16H20l-4 4h14z" />
                <path fill="#008000" d="M6 16v4h10l4-4z" />
                <path fill="#FFD700" d="M20 16V6h-4v14z" />
                <path fill="none" d="M0 0h36v36H0z" />
              </svg>
            </span>
            <span class="btnText">Add Member</span>
          </v-btn>
        </template>
        <v-card id="modal" v-on:keyup.enter="register">
          <v-card-title>
            <span id="modalTitle">Add New Member</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-text-field
                v-model="editedItem.name"
                :rules="[v => !!v || 'Application Name is required']"
                label="Name"
                required
              ></v-text-field>

              <v-text-field v-model="editedItem.email" :rules="emailRules" label="E-mail" required></v-text-field>

              <v-text-field
                v-model="editedItem.password"
                type="password"
                :rules="passwordRules"
                label="Password"
                required
              ></v-text-field>

              <v-text-field
                v-model="editedItem.password_confirmation"
                type="password"
                :rules="confirmPasswordRules"
                label="Confirm Password"
                required
              ></v-text-field>
              <p v-if="show1" class="error">the password you have given do not match</p>

              <v-btn :disabled="!valid" color="success" @click.prevent="register">Submit</v-btn>

              <v-btn color="error" @click="reset">Reset Form</v-btn>

              <v-btn color="warning" @click="resetValidation">Reset Validation</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table :headers="headers" :items="desserts" class="elevation-1 ma-4">
      <template v-slot:items="props">
        <td class="text-xs">{{ props.item.name }}</td>
        <td class="text-xs">{{ props.item.email }}</td>
        <td>
          <v-btn fab small dark color="cyan darken-2" @click="editItem(props.item)">
            <v-icon>edit</v-icon>
          </v-btn>
        </td>
      </template>
    </v-data-table>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data: () => ({
    valid: true,
    allFormData: {
      name: "",
      email: "",
      password: null,
      password_confirmation: null
    },
    dialog: false,
    editedIndex: -1,
    editedItem: {
      name: "",
      email: "",
      password: null,
      password_confirmation: null
    },

    show1: false,

    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+.@.+/.test(v) || "E-mail must be valid"
    ],

    passwordRules: [
      v => !!v || "password is required",
      v => (v && v.length > 5) || "minimum 6 characters"
    ],

    password_confirmation: null,
    confirmPasswordRules: [
      v => !!v || "password is required",
      v => (v && v.length > 5) || "minimum 6 characters"
    ]
  }),
  created() {
    this.fetchData();
  },
  computed: {
    headers() {
      return this.$store.state.userDetails;
    },
    desserts() {
      return this.$store.getters.getUsers;
    }
  },
  methods: {
    fetchData() {
      this.$store.dispatch("getUsers");
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    register() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.$store.dispatch("updateUser", this.editedItem);
          Object.assign(this.desserts[this.editedIndex], this.editedItem);
        } else {
          this.$store.dispatch("registerUser", this.editedItem);

          // this.desserts.push(this.editedItem)
        }
        this.close();
      }
    },
    validate() {
      if (this.allFormData.password != this.allFormData.password_confirmation) {
        return (this.show1 = true);
      } else {
        this.show1 = false;
      }

      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>


