<template>
  <div id="prescription">
    <v-layout row>
      <h1 class="heading grey--text ml-5">Prescription</h1>
      <v-btn absolute dark right class="primary mr-5" @click="save">Save</v-btn>
    </v-layout>
    <v-spacer></v-spacer>

    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs12 sm6 md4>
          <v-text-field outline label="Name" v-model="desserts.name"></v-text-field>
        </v-flex>
        <v-flex xs12 sm6 md2>
          <v-text-field outline label="Age" v-model="desserts.age"></v-text-field>
        </v-flex>
        <v-flex xs12 sm6 md4>
          <v-text-field outline label="Phone" v-model="desserts.contact"></v-text-field>
        </v-flex>
        <v-flex xs12 sm6 md2>
          <v-text-field outline label="Sex" v-model="desserts.gender"></v-text-field>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs10 sm9 md3>
          <!-- <v-text-field name="input-1" label="Styled Input" id="styled-input" class="styled-input"></v-text-field> -->
          <v-text-field
            auto
            v-model="newSymptom"
            v-on:keyup.enter="addSymptoms"
            label="Symptoms"
            :rules="inputRules"
          ></v-text-field>
        </v-flex>
        <v-flex xs2 sm3 md1>
          <v-btn small fab color="primary" @click="addSymptoms">
            <v-icon>add</v-icon>
          </v-btn>
        </v-flex>
        <v-flex xs10 sm9 md3>
          <v-autocomplete
            :items="getTest"
            item-text="name"
            v-model="newTest"
            item-value="id"
            :allow-overflow="false"
            default
            color="purple"
            placeholder="Search..."
            append-icon="$vuetify.icons.dropdown"
            label="Tests"
            v-on:keyup.enter="addTests"
            :rules="inputRules"
          ></v-autocomplete>
          <!-- <v-autocomplete
            v-model="newTest"
            :allow-overflow="false"
            :items="getTest"
            :item-text="name"
            default
            color="purple"
            autofocus
            placeholder="Search..."
            item-value="id"
            hide-no-data
            no-data-text="Add New Member"
          />-->
        </v-flex>
        <v-flex xs2 sm3 md1>
          <v-btn small fab color="primary" @click="addTests">
            <v-icon>add</v-icon>
          </v-btn>
        </v-flex>
        <v-flex xs10 sm9 md4>
          <v-layout column>
            <v-autocomplete
              :items="getMedicine"
              item-text="name"
              item-value="id"
              v-model="newMedicine"
              label="Medicine"
              :rules="inputRules"
            ></v-autocomplete>
            <v-layout row>
              <v-flex md2>
                <v-btn
                  fab
                  small
                  color="primary"
                  @click="dosages.morning = (dosages.morning ? 0 : 1)"
                >{{dosages.morning}}</v-btn>
              </v-flex>
              <v-flex md2>
                <v-btn
                  fab
                  small
                  color="primary"
                  @click="dosages.noon = (dosages.noon ? 0 : 1)"
                >{{dosages.noon}}</v-btn>
              </v-flex>
              <v-flex md2>
                <v-btn
                  fab
                  small
                  color="primary"
                  @click="dosages.night = (dosages.night ? 0 : 1)"
                >{{dosages.night}}</v-btn>
              </v-flex>
              <v-flex>
                <v-text-field
                  label="Days"
                  v-model="dosages.days"
                  v-on:keyup.enter="addMedicines"
                  :rules="inputRules"
                ></v-text-field>
              </v-flex>
              <v-flex>
                <v-btn small fab color="primary" @click="addMedicines">
                  <v-icon>add</v-icon>
                </v-btn>
              </v-flex>
            </v-layout>
          </v-layout>
        </v-flex>
      </v-layout>
      <v-layout row class="mb-2">
        <v-flex xs12 sm6 md4>
          <v-layout row>
            <v-flex md12>
              <ul class="styleUl">
                <li v-for="(symptom,index) in symptoms" :key="symptom">
                  <v-layout row>
                    <v-flex md8>
                      <span>{{++index}}.{{symptom}}</span>
                    </v-flex>
                    <v-flex md4>
                      <v-btn fab small @click="removeSymptom(symptom )" class="red white--text">
                        <v-icon>delete</v-icon>
                      </v-btn>
                    </v-flex>
                  </v-layout>
                </li>
              </ul>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex xs12 sm6 md4>
          <v-layout row>
            <v-flex md12>
              <ul class="styleUl">
                <li class="text-md-left" v-for="(test,index) in tests" :key="test.test_name">
                  <v-layout row>
                    <v-flex md9>
                      <span>{{++index}}.{{test.test_name}}</span>
                    </v-flex>
                    <v-flex md3>
                      <v-btn fab small @click="removeTest(test )" class="red white--text">
                        <v-icon>delete</v-icon>
                      </v-btn>
                    </v-flex>
                  </v-layout>
                </li>
              </ul>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex xs12 sm6 md4>
          <v-layout row>
            <v-flex md12>
              <ul class="styleUl">
                <li v-for="(medicine,index) in medicines" :key="medicine.medicine_name">
                  <v-layout row>
                    <v-flex md10>
                      <v-layout column>
                        <span>{{++index}}.{{medicine.medicine_name}}</span>
                        <span class="dosages">{{medicine.schedule+" ("+medicine.duration+" days)"}}</span>
                      </v-layout>
                    </v-flex>
                    <v-flex md2>
                      <v-btn fab small @click="removeMedicine(medicine)" class="red white--text">
                        <v-icon>delete</v-icon>
                      </v-btn>
                    </v-flex>
                  </v-layout>
                </li>
              </ul>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
      <v-spacer></v-spacer>

      <v-layout class="mt-4" row>
        <v-flex xs12 sm6 md8>
          <v-textarea
            box
            outline
            height="60px"
            label="Suggestion"
            v-model="newSuggestion"
            prepend-icon="edit"
            :rules="inputRules"
          ></v-textarea>
        </v-flex>
        <v-flex xs12 sm6 md4>
          <v-btn absolute dark right class="primary mr-5" @click="save">Save</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import { allSettled } from "q";
export default {
  data: () => ({
    newSymptom: "",
    newMedicine: "",
    newTest: "",
    newSuggestion: "",
    inputRules: [v => !!v || "This field is required"],
    information: {
      name: "",
      age: "",
      contact: ""
    },
    dosages: {
      morning: 1,
      noon: 1,
      night: 1,
      days: ""
    }
    // testName: ""
  }),

  created() {
    this.fetchData();
    this.$store.dispatch("getMedicine");
    this.$store.dispatch("getTest");
    // this.$store.commit("prescriptionDate", this.$route.params.date);
    this.newSuggestion = this.getSuggestion;
  },
  computed: {
    desserts() {
      return this.$store.getters.getPrescriptionHead;
    },
    symptoms() {
      return this.$store.state.symptomsPrescription;
    },
    medicines() {
      return this.$store.state.medicinesPrescription;
    },
    getMedicine() {
      return this.$store.getters.getMedicines;
    },
    getSuggestion() {
      return this.$store.getters.getSuggestion;
    },
    tests() {
      this.newTest = "";
      return this.$store.getters.getTestName;
    },
    getTest() {
      return this.$store.getters.getTests;
    },
    prescriptionId() {
      return this.$store.getters.prescriptionId;
    }
  },

  methods: {
    save() {
      this.$store.commit("addSuggestion", this.newSuggestion);
      this.$store.dispatch("savePrescription").then(response => {
        this.$router.push("/prescriptions/" + response.data.data.id);
      });
    },
    getLabel(item) {
      return item.name;
    },
    fetchData() {
      this.$store.dispatch("patientById", this.$route.params.id);
      this.$store.commit("prescriptionDate", this.$route.params.date);
      this.$store.commit(
        "prescriptionScheduleId",
        this.$route.params.schedule_id
      );
    },
    addSymptoms() {
      this.$store.commit("addSymptoms", this.newSymptom);
      this.newSymptom = "";
    },
    removeSymptom(symptom) {
      const index = this.$store.state.symptomsPrescription.indexOf(symptom);
      this.$store.state.symptomsPrescription.splice(index, 1);
    },
    addMedicines() {
      for (var i = 0; i < this.getMedicine.length; i++) {
        if (this.getMedicine[i]["id"] == this.newMedicine) {
          this.$store.commit({
            type: "addMedicines",
            medicine: this.getMedicine[i],
            dosages: this.dosages
          });
          break;
        }
      }
      // this.$store.dispatch({
      //   type: "addMedicines",
      //   medicine: this.newMedicine,
      //   dosages: this.dosages
      // });
      this.newMedicine = "";
      this.dosages = {
        morning: 1,
        noon: 1,
        night: 1
      };
    },
    removeMedicine(medicine) {
      const index = this.$store.state.medicinesPrescription.indexOf(medicine);
      this.$store.state.medicinesPrescription.splice(index, 1);
      // const index = this.medicines.indexOf(medicine);
      // this.medicines.splice(index, 1);
    },
    addTests() {
      for (var i = 0; i < this.getTest.length; i++) {
        if (this.getTest[i]["id"] == this.newTest) {
          this.$store.commit("addTests", this.getTest[i]);
          this.newTest = "";

          break;
        }
      }
    },
    removeTest(test) {
      const index = this.$store.state.testsPrescription.indexOf(test);
      this.$store.state.testsPrescription.splice(index, 1);
      // const index = this.medicines.indexOf(test);
      // this.tests.splice(index, 1);
    }
  }
};
</script>


<style>
.styleUl {
  margin: 0;
  padding: 0;
}

.styleUl li {
  cursor: pointer;
  position: relative;
  padding: 12px 8px 12px 40px;
  background: #eee;
  font-size: 18px;
  transition: 0.2s;
}

ul.styleUl {
  list-style-type: none;
}

.styleUl li:hover {
  background: #0000;
}

.styleUl li:nth-child(odd) {
  background: #f9f9f9;
}

.dosages {
  margin-left: 14px;
  color: grey;
}

