<template>
  <div id="printableArea">
    <v-layout row wrap id="print">
      <v-flex xs12 sm4 md10>
        <h1 class="heading grey--text ml-5">Prescription</h1>
      </v-flex>
      <v-flex xs12 sm4 md2>
        <v-layout row wrap>
          <!-- <v-flex xs12 sm4 md6>
                        <v-btn dark right class="primary white--text mr-1" to="/prescriptions/prescription"><v-icon>arrow_back</v-icon><span> Back</span></v-btn>
          </v-flex>-->
          <v-flex xs12 sm4 md6>
            <v-btn
              dark
              right
              class="primary white--text mr-5"
              @click.prevent="printPrescription('printableArea')"
            >
              <v-icon>print</v-icon>
              <span>Print</span>
            </v-btn>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
    <!-- <style>
  :root {
    --header-space: {{ getPrintHeaderDetails.header }} || null;
    --footer-space: {{ getPrintHeaderDetails.footer }} || null;
  }
    </style>-->
    <v-container grid-list-md>
      <v-layout row wrap v-if="headerType === 'withHeader'">
        <v-flex xs4 sm4 md4>
          <h2 class="heading green--text">{{ getPrintHeaderDetails.chamber_name }}</h2>
          <p>
            {{ getPrintHeaderDetails.phone}}
            <br />
            {{ getPrintHeaderDetails.mobile}}
            <br />
            {{ getPrintHeaderDetails.address}}
          </p>
        </v-flex>
        <v-flex xs4 sm4 md4>
          <v-img src="../square.png" class="ml-4 mt-4" id="img"></v-img>
        </v-flex>
        <v-flex xs4 sm4 md4>
          <h2 class="heading green--text text-lg-right">{{ getPrintHeaderDetails.doctor_name}}</h2>
          <p class="text-lg-right">{{ getPrintHeaderDetails.degree}}</p>
        </v-flex>
      </v-layout>
      <v-layout :style="headerSpace" v-else></v-layout>
      <hr />
      <br />
      <v-layout row wrap class="patientHead">
        <v-flex xs4 sm4 md4 class="headeritem">
          Name:
          <br />
          <h4>
            <span>{{ desserts.name }}</span>
          </h4>
        </v-flex>
        <v-flex xs4 sm4 md2 class="headeritem">
          Age:
          <br />
          <h4>
            <span>{{ desserts.age }}</span>
          </h4>
        </v-flex>
        <v-flex xs4 sm4 md3 class="headeritem">
          Phone No:
          <br />
          <h4>
            <span>{{ desserts.contact }}</span>
          </h4>
        </v-flex>
        <v-flex xs4 sm4 md2 class="headeritem">
          Sex:
          <br />
          <h4>
            <span>{{ desserts.gender }}</span>
          </h4>
        </v-flex>
      </v-layout>
      <br />
      <v-layout row wrap class="mb-4">
        <v-flex xs4 sm4 md4 class="left">
          <v-layout column>
            <v-flex xs12 sm6 md6 class="symptomp">
              <h2 class="subheading grey--text ml-2">symptoms</h2>
              <v-list dense class="ml-5">
                <v-list-tile v-for="(symptom,index) in symptoms" :key="symptom">
                  <v-list-tile-content>
                    <v-list-tile-title>
                      <span>{{++index}}.</span>
                      {{ symptom }}
                    </v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list>
            </v-flex>
            <v-flex xs12 sm6 md6 class="test">
              <h2 class="subheading grey--text ml-2">Tests</h2>
              <v-list dense class="ml-5">
                <v-list-tile v-for="(test,index) in tests" :key="test.test_name">
                  <v-list-tile-content>
                    <v-list-tile-title>
                      <span>{{++index}}.</span>
                      {{ test.test_name }}
                    </v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex xs8 sm8 md8 class="right">
          <h2 class="subheading grey--text ml-2">Medicines</h2>
          <v-list dense class="ml-5">
            <v-list-tile v-for="(medicine,index) in medicines" :key="medicine.medicine_name">
              <v-list-tile-content>
                <v-list-tile-title>
                  <span>{{++index}}.</span>
                  {{ medicine.medicine_name}}
                </v-list-tile-title>
                <v-list-tile-sub-title
                  class="ml-3"
                >{{medicine.schedule +" ("+medicine.duration+" days)"}}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-flex>
      </v-layout>
      <v-layout class="suggestions mt-5">
        <v-flex>
          <h4>suggestion</h4>
          <p class="paragraph">{{ suggestions }}</p>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  created() {
    this.fetchData();
    this.getPrintHeader();
  },
  computed: {
    desserts() {
      return this.$store.getters.getPrescriptionHead;
    },
    symptoms() {
      return this.$store.state.symptomsPrescription;
    },
    medicines() {
      return this.$store.state.medicinesPrescription;
    },
    tests() {
      return this.$store.state.testsPrescription;
    },
    suggestions() {
      return this.$store.state.suggestionsPrescription;
    },
    headerType() {
      return this.$store.state.headerType;
    },
    getPrintHeaderDetails() {
      return this.$store.getters.getPrintHeaderDetails;
    },
    headerSpace() {
      if (this.headerType == "withoutHeader") {
        return {
          height: this.getPrintHeaderDetails.header + "cm"
        };
      }
    }
  },
  methods: {
    getPrintHeader() {
      this.$store.dispatch("getPrintHeaderDetails");
    },
    printPrescription(divName) {
      var printContents = document.getElementById(divName).innerHTML;
      var originalContents = document.body.innerHTML;

      document.body.innerHTML = printContents;

      window.print();

      document.body.innerHTML = originalContents;
    },
    fetchData() {
      this.$store.dispatch("getPrescription", this.$route.params.id);
    }
  }
};
</script>


