<template>
  <div>
    <v-toolbar flat color="white">
      <v-toolbar-title class="grey--text">Settings</v-toolbar-title>
      <v-divider class="mx-2" inset vertical></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog2" max-width="600px" class="abc">
        <template v-slot:activator="{ on }">
          <v-btn class="btn" v-on="on">
            <span aria-hidden="true" class="btnLogo">
              <svg width="36" height="36" viewBox="0 0 36 36">
                <path fill="#FF0000" d="M16 16v14h4V20z" />
                <path fill="#000080" d="M30 16H20l-4 4h14z" />
                <path fill="#008000" d="M6 16v4h10l4-4z" />
                <path fill="#FFD700" d="M20 16V6h-4v14z" />
                <path fill="none" d="M0 0h36v36H0z" />
              </svg>
            </span>
            <span class="btnText">Create Slot</span>
          </v-btn>
        </template>
        <v-card id="modal" v-on:keyup.enter="save">
          <v-card-title>
            <span id="modalTitle">{{ formTitle }}</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-form class="px-3" ref="form" id="form">
              <v-layout wrap>
                <v-flex md12 lg6>
                  <h4>Starting Time</h4>
                  <v-time-picker
                    v-model="editedItem.start_time"
                    width="220"
                    scrollable
                    :rules="inputRules"
                  ></v-time-picker>
                </v-flex>
                <v-flex md12 lg6>
                  <h4>Ending Time</h4>
                  <v-time-picker
                    v-model="editedItem.end_time"
                    width="220"
                    scrollable
                    :rules="inputRules"
                  ></v-time-picker>
                </v-flex>
              </v-layout>
            </v-form>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-layout wrap>
      <v-flex md12>
        <v-data-table :headers="headers" :items="desserts" class="elevation-1 ma-4">
          <template v-slot:items="props">
            <td class="text-xs">{{ props.item.id }}</td>
            <td class="text-xs">{{ props.item.start_time }}</td>
            <td class="text-xs">{{ props.item.end_time }}</td>

            <td class="text-xs layout px-0">
              <v-tooltip top>
                <v-btn small fab dark color="cyan" slot="activator" @click="editItem(props.item)">
                  <v-icon>edit</v-icon>
                </v-btn>
                <span>Edit Doctor Details</span>
              </v-tooltip>
            </td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import VueTimepicker from "vue2-timepicker";
import UploadButton from "vuetify-upload-button";

export default {
  components: {
    "upload-btn": UploadButton
  },
  data: () => ({
    dialog1: false,
    dialog2: false,
    TimeValue: null,
    inputRules: [v => !!v || "This Field is required"],
    editedIndex: -1,
    editedItem: {
      start_time: "",
      end_time: ""
    },
    defaultItem: {
      start_time: null,
      end_time: null
    }
  }),
  created() {
    this.fetchData();
    // this.fetchFilteredData();
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Add New Slot" : "Edit Slot";
    },
    formTitleHeader() {
      return this.editedIndex === -1 ? "Add Header" : "Edit Slot";
    },
    headers() {
      return this.$store.state.timeSlotHeaders;
    },
    desserts() {
      return this.$store.getters.getTimeSlots;
    }
  },
  watch: {
    dialog1(val) {
      val || this.close();
    },
    dialog2(val) {
      val || this.close();
    }
  },
  methods: {
    fetchData() {
      this.$store.dispatch("getTimeSlot");
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog2 = true;
    },
    close() {
      this.dialog1 = false;
      this.dialog2 = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.$store.dispatch("updateTimeSlot", this.editedItem);
          Object.assign(this.desserts[this.editedIndex], this.editedItem);
        } else {
          this.$store.dispatch("saveTimeSlot", this.editedItem);
        }
        this.close();
      }
    }
  }
};
</script>

