<template>
  <div id="vue-app">
    <v-toolbar flat color="white">
      <v-toolbar-title class="grey--text">Patients List</v-toolbar-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <template v-slot:extension>
        
      </template>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn class="btn" v-on="on">
            <span aria-hidden="true" class="btnLogo">
              <svg width="36" height="36" viewBox="0 0 36 36">
                <path fill="#FF0000" d="M16 16v14h4V20z"></path>
                <path fill="#000080" d="M30 16H20l-4 4h14z"></path>
                <path fill="#008000" d="M6 16v4h10l4-4z"></path>
                <path fill="#FFD700" d="M20 16V6h-4v14z"></path>
                <path fill="none" d="M0 0h36v36H0z"></path>
              </svg>
            </span>
            <span class="btnText">Create Patient</span>
          </v-btn>
        </template>
        <v-card id="modal">
          <v-card-title>
            <span id="modalTitle">{{ formTitle }}</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>

            <v-combobox
              v-model="searchPatients.searchInput"
              :items="searchFilter"
              :search-input.sync="searchPatient"
              item-text="contact"
              item-value="name"
              item-disabled
              hint="Search by Contact Number"
              label="Search Old Patient"
              prepend-inner-icon="search"
              outline
              open-on-clear
              @input="addOldPatientSchedule"
            >
              <template v-slot:item="data">
                <v-list-tile-content >
                  <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                  <v-list-tile-sub-title v-html="'Contact: ' +data.item.contact +', Age: '+ data.item.age"></v-list-tile-sub-title>
                </v-list-tile-content>
               <!-- <v-btn small fab dark color="cyan darken-2" slot="activator" @click="addOldPatientSchedule(data.item)">
                    <v-icon>add</v-icon>
                </v-btn> -->
              </template>
            </v-combobox>
          </v-card-text>
          <v-card-text>
              <v-form class="px-3" ref="form">
                <v-text-field label="Patient Name" v-model="editedItem.name" prepend-icon="add_box" :rules="inputRules"></v-text-field>
                <v-text-field label="Age" v-model="editedItem.age" prepend-icon="add_to_queue" type="number" :rules="inputRules"></v-text-field>
                <v-text-field label="Contact" v-model="editedItem.contact" prepend-icon="settings_phone" type="number" :rules="contactRules"></v-text-field>
                <!-- <v-text-field label="Doctor Name" v-model="editedItem.doctor_name" prepend-icon="add_to_queue" :rules="inputRules"></v-text-field> -->
                <v-select
                v-model="editedItem.gender"
                :items="gender"
                label="Select Gender"
                prepend-icon="person"
                >
                  <template v-slot:item="props">
                    <v-list-tile-content >
                      <v-list-tile-title v-html="props.item"></v-list-tile-title>
                    </v-list-tile-content>
                  </template>
                </v-select>
                <v-menu>
                  <v-text-field :value="viewDateFormate(editedItem.date)" slot="activator" label="Select Date" prepend-icon="date_range" :rules="inputRules"></v-text-field>
                  <v-date-picker v-model="editedItem.date"></v-date-picker>
                </v-menu>
                  <v-select
                  v-model="editedItem.time"
                  :items="getTimeSlot"
                  item-text="time"
                  item-value="id"
                  label="Select Time"
                  prepend-icon="access_time"
                  >
                    <template v-slot:item="data">
                      <v-list-tile-content >
                        <v-list-tile-title v-html="data.item.start_time + '-' + data.item.end_time"></v-list-tile-title>
                        <!-- <v-list-tile-sub-title v-html="'Contact: ' +data.item.contact +', Age: '+ data.item.age"></v-list-tile-sub-title> -->
                      </v-list-tile-content>
                    </template>
                  </v-select>
              </v-form>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-layout wrap>
      <v-flex md8>
        <v-layout>
        <v-flex md5>
        <v-menu class="ml-4">
          <v-text-field :value="viewDateFormate(searchPatientsBy.date)" slot="activator" label="Filter by date" prepend-icon="date_range"></v-text-field>
          <v-date-picker v-model="searchPatientsBy.date"></v-date-picker>
        </v-menu>
        </v-flex>
         <v-flex md5>
        <v-select
        v-model="searchPatientsBy.time"
        :items="getTimeSlot"
        item-text="time"
        item-value="id"
        label="Select Time"
        prepend-icon="access_time"
        >
          <template v-slot:item="data">
            <v-list-tile-content >
              <v-list-tile-title v-html="data.item.start_time + '-' + data.item.end_time"></v-list-tile-title>
            </v-list-tile-content>
          </template>
        </v-select>
      </v-flex>
      <v-flex md2>
      <v-btn class="primary" flat @click="retrievePatientbyDate">Search</v-btn>
      </v-flex>
      </v-layout>
      </v-flex>
      <v-flex md4>
      </v-flex>
    </v-layout>
    <v-data-table
      :headers="headers"
      :items="desserts"
      class="elevation-1 ma-4"
    >
      <template v-slot:items="props">
        <td class="text-xs">{{ props.item.serial }}</td>
        <td class="text-xs"><v-chip color="cyan" text-color="white">{{ props.item.name }}</v-chip></td>
        <td class="text-xs">{{ props.item.age }}</td>
        <td class="text-xs">{{ props.item.gender }}</td>
        <td class="text-xs">{{ props.item.contact }}</td>
        <td class="text-xs">{{ viewDateFormate(props.item.date) }}</td>
        <td class="text-xs">{{ props.item.time }}</td>
        <td class="text-xs">
            <v-tooltip top>
                <v-btn small fab dark color="cyan darken-2" slot="activator" @click="editItem(props.item)">
                    <v-icon>edit</v-icon>
                </v-btn>
                <span>Edit Patient Details</span>
            </v-tooltip>
            <v-tooltip top>
                <v-btn small fab dark color="indigo" v-if="userInfo.isadmin" slot="activator" router: v-bind:to="'../prescriptions/patient/' + props.item.id + '/'+ props.item.schedule_id + '/' + props.item.date">
                    <v-icon>assignment_ind</v-icon>
                </v-btn>
                <span>Prescription</span>
            </v-tooltip>
             <v-tooltip top>
                <v-btn small fab dark color="purple" slot="activator" @click="getPrescriptionList(props.item)">
                    <span>{{ props.item.visited_count }}</span>
                </v-btn>
                <span>Number of Prescription</span>
            </v-tooltip>
            <v-tooltip top>
                <v-btn small fab dark color="red" slot="activator" @click="deleteItem(props.item)">
                    <v-icon>delete</v-icon>
                </v-btn>
                <span>Delete Patient</span>
            </v-tooltip>
        </td>
      </template>
      <template v-slot:no-data>
        <h4>No Patient</h4>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import Format from 'date-fns/format'


  export default {
    data: () => ({
      // search: null,
      autoUpdate: true,
      searchPatient: null,
      searchPatients: { 
        searchInput: ''
      },
      searchPatientsBy: {
        date: '',
        time: 1
      },
      gender: [],
      dialog: false,
      inputRules: [
        (v) => !!v || 'This Field is required',
      ],
      contactRules: [
        (v) => !!v || 'This Field is required',
        (v) => (v && v.length == 11) || 'Contact length must be 11 characters',

      ],
      editedIndex: -1,
      editedItem: {
        name: '',
        age: '',
        contact: '',
        doctor_name: '',
        date: '',
        searchDate: '',
        time: '',
        gender: ''
      },
      defaultItem: {
        name: '',
        age: '',
        contact: '',
        doctor_name: '',
        date: '',
        time: '',
        gender: ''
      }
    }),
    created () {
      this.fetchData()
      this.initialize()
    },

    computed: {
      jointPatientsDetails(){
        return this.$store.getters.getJointPatientsDetails;
      },
      desserts(){
        return this.$store.getters.getPatients
        
      },
      userInfo() {
        return this.$store.getters.getUserInfo;
      },
      patientInfo() {
        return this.$store.getters.getPatientsBySearch
      },
      searchFilter(){
        return this.patientInfo.filter((getPatient) => {
          return (getPatient.name.toLowerCase().match(this.searchPatient) || getPatient.contact.match(this.searchPatient))
        });
      },
      formTitle () {
        return this.editedIndex === -1 ? 'Add Patient' : 'Edit Patient'
      },
      formattedDate (){
        return this.date ? Format(this.date, 'YYYY MMM Do'): ''
      },
      headers(){
        return this.$store.state.patientsHeaders;
      },
      getTimeSlot(){
        return this.$store.getters.getTimeSlots;
      }
        
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      searchPatient (val) {
        this.retrievePatientbySearch(val)
      }
    },
    
    methods: {
      fetchData() {
        this.$store.dispatch('getPatient')
        .then(response => {
          this.searchPatientsBy.date = response.data.data.date
          this.searchPatientsBy.time = response.data.data.time.id
        })
        this.$store.dispatch("getTimeSlot");

      },
      initialize(){
        this.gender = ['Male', 'Female', 'Others']
      },
      retrievePatientbyDate(){
        this.$store.dispatch('retrievePatientbyDate', this.searchPatientsBy);
      },
      retrievePatientbySearch(searchBy){
          var searchInfo = {
            'searchInput' : searchBy
          }
        this.$store.dispatch('retrievePatientbySearch', searchInfo);
      },
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      addOldPatientSchedule(item){
         this.editedIndex = this.searchFilter.indexOf(item)
        this.editedItem = Object.assign({}, item)
      },
      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        if(confirm('Are you sure you want to delete this item?')){  
          this.desserts.splice(index, 1);
          this.$store.dispatch('deletePatient',item.id);
        }
        },
      close () {
        this.dialog = false
        setTimeout(() => {
          this.defaultItem.time = this.$store.getters.getTimeSlots.start_time + '-'+ this.$store.getters.getTimeSlots.end_time
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      save () {
         if (this.$refs.form.validate()) {

          if (this.editedIndex > -1) {
            this.$store.dispatch('updatePatient', this.editedItem);
            Object.assign(this.desserts[this.editedIndex], this.editedItem)
            this.searchPatients.searchInput = '';
          } else {
            this.$store.dispatch('savePatient',this.editedItem)
            // this.desserts.push(this.editedItem)
          }
          this.close()
        }
      },
      viewDateFormate (date){
            return date ? Format(date, 'YYYY MMM Do'): ''
      },
      attachSearchValue(item){
         this.editedIndex = this.searchFilter.indexOf(item)
        this.editedItem = Object.assign({}, item)
      },
      getPrescriptionList(item){
        this.$router.push("/patientsReport/report/" + item.id)
      },
    }
  }
</script>

