<template>
  <div>
    <v-toolbar flat color="white">
      <v-toolbar-title class="grey--text">Tests List</v-toolbar-title>
      <v-divider class="mx-2" inset vertical></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn class="btn" v-on="on">
            <span aria-hidden="true" class="btnLogo">
              <svg width="36" height="36" viewBox="0 0 36 36">
                <path fill="#FF0000" d="M16 16v14h4V20z" />
                <path fill="#000080" d="M30 16H20l-4 4h14z" />
                <path fill="#008000" d="M6 16v4h10l4-4z" />
                <path fill="#FFD700" d="M20 16V6h-4v14z" />
                <path fill="none" d="M0 0h36v36H0z" />
              </svg>
            </span>
            <span class="btnText">Create Test</span>
          </v-btn>
        </template>
        <v-card id="modal" v-on:keyup.enter="save">
          <v-form class="px-3" ref="form">
            <v-card-title>
              <span id="modalTitle">{{ formTitle }}</span>
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text>
              <div class="px-3">
                <v-text-field
                  label="Test Name"
                  v-model="editedItem.name"
                  prepend-icon="add_box"
                  :rules="nameRules"
                ></v-text-field>
                <v-text-field
                  label="Group"
                  v-model="editedItem.group"
                  prepend-icon="add_to_queue"
                  :rules="groupRules"
                ></v-text-field>
              </div>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table :headers="headers" :items="desserts" class="elevation-1 ma-4">
      <template v-slot:items="props">
        <td class="text-xs">{{ props.item.name }}</td>
        <td class="text-xs">{{ props.item.group }}</td>
        <td class="text-xs layout px-0">
          <v-btn small fab dark color="cyan" class="mr-2" @click="editItem(props.item)">
            <v-icon>edit</v-icon>
          </v-btn>
          <v-btn small fab dark color="red" class="mr-2" @click="deleteItem(props.item)">
            <v-icon>delete</v-icon>
          </v-btn>
        </td>
      </template>
    </v-data-table>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,

    editedIndex: -1,
    editedItem: {
      name: "",
      group: ""
    },
    defaultItem: {
      name: "",
      group: ""
    },
    nameRules: [v => !!v || "Name is required"],
    groupRules: [v => !!v || "Group is required"]
  }),
  computed: {
    desserts() {
      return this.$store.getters.getTests;
    },
    formTitle() {
      return this.editedIndex === -1 ? "Add New Test" : "Edit Test";
    },
    headers() {
      return this.$store.state.testsHeaders;
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {
    // this.initialize()
    this.$store.dispatch("getTest");
  },
  methods: {
    reset() {},
    // initialize () {
    //   this.desserts = [
    //     {
    //       name: 'Frozen Yogurt',
    //       group: 'Frozen',
    //     },
    //     {
    //       name: 'Ice cream sandwich',
    //       group: 'cream',
    //     },

    //   ]
    // },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem(item) {
      const index = this.desserts.indexOf(item);
      if (confirm("Are you sure you want to delete this item?")) {
        this.desserts.splice(index, 1);
        this.$store.dispatch("deleteTest", item.id);
      }
    },
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.$store.dispatch("updateTest", this.editedItem);

          Object.assign(this.desserts[this.editedIndex], this.editedItem);
        } else {
          this.$store.dispatch("saveTest", this.editedItem);
          // this.desserts.push(this.editedItem)
        }
        this.close();
      }
    }
  }
};
</script>

