<template>
  <div id>
    <v-toolbar flat color="white">
      <v-toolbar-title class="grey--text">Doctors List</v-toolbar-title>
      <v-divider class="mx-2" inset vertical></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn class="btn" v-on="on">
            <span aria-hidden="true" class="btnLogo">
              <svg width="36" height="36" viewBox="0 0 36 36">
                <path fill="#FF0000" d="M16 16v14h4V20z"></path>
                <path fill="#000080" d="M30 16H20l-4 4h14z"></path>
                <path fill="#008000" d="M6 16v4h10l4-4z"></path>
                <path fill="#FFD700" d="M20 16V6h-4v14z"></path>
                <path fill="none" d="M0 0h36v36H0z"></path>
              </svg>
            </span>
            <span class="btnText">Create Doctor</span>
          </v-btn>
        </template>
        <v-card id="modal">
          <v-card-title>
            <span id="modalTitle">{{ formTitle }}</span>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-form class="px-3" ref="form">
              <v-text-field
                label="Full Name"
                v-model="editedItem.name"
                prepend-icon="edit"
                :rules="inputRules"
              ></v-text-field>
              <v-text-field
                label="Email"
                v-model="editedItem.email"
                prepend-icon="email"
                :rules="emailRules"
              ></v-text-field>
              <v-text-field
                label="Contact"
                v-model="editedItem.contact"
                type="number"
                prepend-icon="contacts"
                :rules="contactRules"
              ></v-text-field>
              <v-text-field
                label="Work Area"
                v-model="editedItem.workarea"
                prepend-icon="local_hospital"
                :rules="inputRules"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table :headers="headers" :items="desserts" class="elevation-1 ma-4">
      <template v-slot:items="props">
        <td class="text-xs">{{ props.item.name }}</td>
        <td class="text-xs">{{ props.item.email }}</td>
        <td class="text-xs">{{ props.item.contact }}</td>
        <td class="text-xs">{{ props.item.workarea }}</td>
        <td class="text-xs">{{ props.item.patient_no }}</td>

        <td class="text-xs layout px-0">
          <v-tooltip top>
            <v-btn small fab dark color="primary" slot="activator">
              <v-icon>pageview</v-icon>
            </v-btn>
            <span>View Details</span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn small fab dark color="cyan" slot="activator" @click="editItem(props.item)">
              <v-icon>edit</v-icon>
            </v-btn>
            <span>Edit Doctor Details</span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn small fab dark color="red" slot="activator" @click="deleteItem(props.item)">
              <v-icon>delete</v-icon>
            </v-btn>
            <span>Delete Doctor</span>
          </v-tooltip>
        </td>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    inputRules: [v => !!v || "This Field is required"],
    contactRules: [
      v => !!v || "This Field is required",
      v => (v && v.length == 11) || "Contact length must be 11 characters"
    ],
    emailRules: [
      v =>
        v.match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        ) || "Invalid Email address"
    ],

    desserts: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      email: "",
      contact: "",
      workarea: "",
      patient_no: ""
    },
    defaultItem: {
      name: "",
      email: "",
      contact: "",
      workarea: "",
      patient_no: ""
    }
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Add New Doctor" : "Edit Doctor";
    },
    headers() {
      return this.$store.state.doctorsHeaders;
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.desserts = [
        {
          name: "Marc Moreno",
          email: "marc@gmail.com",
          contact: "01711111111",
          workarea: "Medicine",
          patient_no: "1"
        },
        {
          name: "Wallace  Frank",
          email: "marc@gmail.com",
          contact: "01711111111",
          workarea: "Medicine",
          patient_no: "10"
        }
      ];
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem(item) {
      const index = this.desserts.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.desserts.splice(index, 1);
    },
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem);
        } else {
          this.$http.post(
            "http://local.prescription.com/api/v1/doctors",
            this.editedItem
          );
          this.desserts.push(this.editedItem);
        }
        this.close();
      }
    }
  }
};
</script>

