<template>
  <div>
    <v-container>
      <v-toolbar flat color="white">
        <v-toolbar-title class="grey--text">Prescription Header Details</v-toolbar-title>
      </v-toolbar>
      <v-radio-group v-model="prescriptionType" row>
        <v-radio label="With Header" value="withHeader" color="red"></v-radio>
        <v-radio label="Without Header" value="withoutHeader" color="red"></v-radio>
      </v-radio-group>
      <v-form class="px-3" ref="form" id="form" v-if="prescriptionType === 'withHeader'">
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex md9>
              <h4>Doctor Details</h4>
              <v-layout wrap ml-2>
                <v-flex xs12>
                  <v-text-field label="Name" v-model="headerItem.doctorName"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field label="Degree" v-model="headerItem.degree"></v-text-field>
                </v-flex>
              </v-layout>
              <h4>Chamber Details</h4>
              <v-layout wrap ml-2>
                <v-flex xs12>
                  <v-text-field label="Name" v-model="headerItem.chamberName"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field label="Address" v-model="headerItem.address"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field label="Telephone" v-model="headerItem.telephone"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field label="Mobile" v-model="headerItem.cellphone"></v-text-field>
                </v-flex>
              </v-layout>
            </v-flex>
            <v-divider class="mx-2" inset vertical></v-divider>
            <v-flex md2>
              <v-layout wrap>
                <v-flex xs12>
                  <!-- <upload-btn title="Upload image" @file-update="update"></upload-btn> -->
                  <label>
                    File
                    <v-btn raised @click="onPickFile">Upload Image</v-btn>
                    <input
                      type="file"
                      id="file"
                      ref="fileInput"
                      style="display:none"
                      accept="image/*"
                      @change="onFilePicked"
                    />
                  </label>
                  <v-flex ml-3 mt-3>
                    <img :src="imageUrl" height="150" />
                  </v-flex>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-container>
        <v-divider></v-divider>
        <!-- <v-spacer></v-spacer> -->
        <v-layout wrap>
          <v-spacer></v-spacer>
          <v-btn color="warning darken-1" dark @click="clear">Clear</v-btn>
          <v-btn color="primary" dark @click="savePrintHeaderDetails">Save</v-btn>
        </v-layout>
      </v-form>
      <v-form class="px-3" v-if="prescriptionType === 'withoutHeader'">
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12 md6 class="text-lg-center">
              <v-text-field label="Header Space (in cm)" v-model="withoutHeaderItem.headerSpace"></v-text-field>
              <v-text-field label="Footer Space (in cm)" v-model="withoutHeaderItem.footerSpace"></v-text-field>
            </v-flex>
            <v-flex md6></v-flex>
            <v-spacer></v-spacer>
            <v-btn color="warning darken-1" dark @click="clear">Clear</v-btn>
            <v-btn color="primary" dark @click="saveEmptyHeader">Save</v-btn>
          </v-layout>
        </v-container>
      </v-form>
    </v-container>
  </div>
</template>

<script>
import UploadButton from "vuetify-upload-button";

export default {
  components: {
    "upload-btn": UploadButton
  },
  data: () => ({
    prescriptionType: "",
    headerItem: {
      doctorName: "",
      degree: "",
      chamberName: "",
      telephone: "",
      cellphone: "",
      address: "",
      image: "",
      prescriptionType: ""
    },
    withoutHeaderItem: {
      headerSpace: "",
      footerSpace: ""
    },
    imageUrl: "",
    image: null
  }),
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.$store.dispatch("getPrintHeaderDetails").then(response => {
        this.headerItem.doctorName = response.data.data.withHeader.doctor_name;
        this.headerItem.degree = response.data.data.withHeader.degree;
        this.headerItem.chamberName =
          response.data.data.withHeader.chamber_name;
        this.headerItem.telephone = response.data.data.withHeader.phone;
        this.headerItem.cellphone = response.data.data.withHeader.mobile;
        this.headerItem.address = response.data.data.withHeader.address;
        this.withoutHeaderItem.headerSpace =
          response.data.data.withoutHeader.header;
        this.withoutHeaderItem.footerSpace =
          response.data.data.withoutHeader.footer;
        this.prescriptionType = response.data.data.headerType.value;
      });
    },
    clear() {
      this.headerItem = "";
    },
    update(file) {
      // handle file here. File will be an object.
      // If multiple prop is true, it will return an object array of files.
      this.headerItem.logo = file;
    },
    savePrintHeaderDetails() {
      // let formData = new FormData();
      // formData.append("file", this.file);
      this.headerItem.prescriptionType = this.prescriptionType;
      this.$store.dispatch("savePrintHeaderDetails", this.headerItem);
    },
    // handleFileUpload() {
    //   this.file = this.$refs.file.files[0];
    // },
    onPickFile() {
      this.$refs.fileInput.click();
    },
    onFilePicked(event) {
      const files = event.target.files;
      let filename = files[0].name;
      if (filename.lastIndexOf(".") <= 0) {
        return alert("Please add a valid file!");
      }
      const fileReader = new FileReader();
      fileReader.addEventListener("load", () => {
        this.imageUrl = fileReader.result;
      });
      fileReader.readAsDataURL(files[0]);
      this.image = files[0];
      this.headerItem.image = this.image;
    },
    saveEmptyHeader() {
      this.withoutHeaderItem.prescriptionType = this.prescriptionType;
      this.$store.dispatch("savePrintHeaderDetails", this.withoutHeaderItem);
    }
  }
};
</script>