<template>
  <v-container v-on:keyup.enter="register">
    <v-layout text-xs-center text-md-center>
      <v-flex xs1 sm1 md2 lg3></v-flex>
      <v-flex xs10 sm1 md8 lg6>
        <v-layout row wrap justify-center class="signup-form-header p-0">
          <h1>Registration to Prescription</h1>
        </v-layout>
        <!--  -->
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="allFormData.name"
            :rules="[v => !!v || 'Application Name is required']"
            label="Name"
            required
          ></v-text-field>

          <v-text-field v-model="allFormData.email" :rules="emailRules" label="E-mail" required></v-text-field>

          <v-text-field
            v-model="allFormData.password"
            type="password"
            :rules="passwordRules"
            label="Password"
            required
          ></v-text-field>

          <v-text-field
            v-model="allFormData.password_confirmation"
            type="password"
            :rules="confirmPasswordRules"
            label="Confirm Password"
            required
          ></v-text-field>
          <p v-if="show1" class="error">the password you have given do not match</p>

          <v-btn :disabled="!valid" color="success" @click.prevent="register">Submit</v-btn>

          <v-btn color="error" @click="reset">Reset Form</v-btn>

          <v-btn color="warning" @click="resetValidation">Reset Validation</v-btn>
        </v-form>
      </v-flex>
      <v-flex xs1 sm1 md2 lg3></v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data: () => ({
    valid: true,
    allFormData: {
      name: "",
      email: "",
      password: null,
      password_confirmation: null
    },

    show1: false,

    applications: ["inventory", "easyebilling"],

    companies: ["nation communication", "nation electronics", "ctmt ltd"],

    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+.@.+/.test(v) || "E-mail must be valid"
    ],

    passwordRules: [
      v => !!v || "password is required",
      v => (v && v.length > 5) || "minimum 6 characters"
    ],

    password_confirmation: null,
    confirmPasswordRules: [
      v => !!v || "password is required",
      v => (v && v.length > 5) || "minimum 6 characters"
    ]
  }),

  methods: {
    register() {
      this.$store.dispatch("register", this.allFormData).then(response => {
        this.$router.push("/signin");
      });
    },
    validate() {
      if (this.allFormData.password != this.allFormData.password_confirmation) {
        return (this.show1 = true);
      } else {
        this.show1 = false;
      }

      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>


