.signup-form-header {
  background-color: transparent;
  border: 0;
  color: #333;
  margin-bottom: 15px;
  text-align: center;
  text-shadow: none;
}
.auth-form-header h1 {
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -0.5px;
}
