<template>
  <div>
    <v-layout>
      <v-flex>
        <h2 class="grey--text">Prescription List</h2>
      </v-flex>
      <v-flex></v-flex>
    </v-layout>
    <v-spacer></v-spacer>
    <v-layout>
      <v-flex md-4></v-flex>
      <v-flex md-4>
        <v-menu class="ml-4">
          <v-text-field
            :value="viewDateFormate(searchDate.date)"
            slot="activator"
            label="Filter by date"
            prepend-icon="date_range"
          ></v-text-field>
          <v-date-picker v-model="searchDate.date" @input="retrievePrescriptionbyDate"></v-date-picker>
        </v-menu>
      </v-flex>
      <v-flex md4></v-flex>
    </v-layout>
    <v-spacer></v-spacer>
    <v-data-table :headers="headers" :items="desserts" class="elevation-1 ma-4">
      <template v-slot:items="props">
        <td class="text--xs">{{ props.item.patient.name }}</td>
        <td class="text--xs">{{ props.item.patient.contact }}</td>
        <td class="text--xs">{{ props.item.created_at }}</td>
        <td class="text--xs">{{ showSymptom(props.item) }}</td>
        <td class="text--xs">{{ showMedicine(props.item) }}</td>
        <td class="text--xs">{{ showTests(props.item) }}</td>
        <td class="text--xs">
          <v-btn flat class="primary" @click="showPrescription(props.item)">Show</v-btn>
        </td>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import Format from "date-fns/format";

export default {
  data: () => ({
    searchPatient: null,
    searchPatients: {
      searchInput: ""
    },
    searchDate: {
      date: ""
    }
  }),
  // created() {
  //   this.fetchData();
  // },
  computed: {
    headers() {
      return this.$store.state.prescriptionHeadByDate;
    },
    desserts() {
      return this.$store.getters.getPrescriptionByDate;
    }
  },

  methods: {
    // fetchData() {
    //   if (typeof this.$route.params.id != "undefined") {
    //     this.$store.dispatch("getPrescriptionReport", {
    //       id: this.$route.params.id
    //     });
    //   }
    // },
    showTests(item) {
      var twoTests = item.tests.slice(0, 2);

      var testsInfo = "";

      twoTests.map(test => {
        testsInfo += test.name + ", ";
      });

      return testsInfo;
    },
    showSymptom(item) {
      var twoSymptom = item.prescription_symptom.slice(0, 2);

      var symptomInfo = "";

      twoSymptom.map(symptom => {
        symptomInfo += symptom.symptoms + ", ";
      });

      return symptomInfo;
    },
    showMedicine(item) {
      var twoMedicines = item.medicines.slice(0, 2);

      var medicineInfo = "";

      twoMedicines.map(medicine => {
        medicineInfo += medicine.name + ", ";
      });

      return medicineInfo;
    },
    showPrescription(item) {
      this.$router.push("/prescriptions/" + item.id);
    },
    viewDateFormate(date) {
      return date ? Format(date, "YYYY MMM Do") : "";
    },
    retrievePrescriptionbyDate() {
      this.$store.dispatch("retrievePrescriptionbyDate", this.searchDate);
    }
  }
};
</script>

