import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '../views/Dashboard.vue'
import Members from '../views/members.vue'
import PatientList from '../views/patients/patientsList.vue'
import DoctorList from '../views/doctors/doctorsList.vue'
import MedicineList from '../views/medicines/medicinesList.vue'
import TestList from '../views/tests/testsList.vue'
import Prescription from '../views/prescriptions/prescription.vue'
import PrintPrescription from '../views/prescriptions/print.vue'
import AuthHandler from '../views/auth/authHandler'
import Signin from '../views/auth/signin'
import Signup from '../views/auth/signup'
import Signout from '../views/auth/signout'
import Report from '../views/patientsReport/report'
import ReportByDate from '../views/patientsReport/reportByDate'
import Settings from '../views/settings/timeSlotSettings'
import HeaderSettings from '../views/settings/prescriptionHeaderSettings'
import {
  store
} from '../store/store'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      },

    },
    {
      path: '/members',
      name: 'members',
      component: Members,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        if (store.state.userInfo.isadmin) {
          next();
        } else {
          next("/")
        }
      }
    },
    {
      path: '/patients/patientsList',
      name: 'patientList',
      component: PatientList,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/doctors/doctorsList',
      name: 'doctorList',
      component: DoctorList,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        if (store.state.userInfo.isadmin) {
          next();
        } else {
          next("/")
        }
      }
    },
    {
      path: '/medicines/medicinesList',
      name: 'medicineList',
      component: MedicineList,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        if (store.state.userInfo.isadmin) {
          next();
        } else {
          next("/")
        }
      }
    },
    {
      path: '/tests/testsList',
      name: 'testList',
      component: TestList,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        if (store.state.userInfo.isadmin) {
          next();
        } else {
          next("/")
        }
      }
    },
    {
      path: '/prescriptions/patient/:id/:schedule_id/:date?/:prescription_id?',
      name: 'prescription',
      component: Prescription,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/prescriptions/:id',
      name: 'printPrescription1',
      component: PrintPrescription,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/prescriptions/print',
      name: 'printPrescription',
      component: PrintPrescription,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/oauth2/callback',
      name: 'auth handler',
      component: AuthHandler,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/signin',
      name: 'signin',
      component: Signin,
      meta: {
        requiresVisitor: true
      }
    },
    {
      path: '/signup',
      name: 'signup',
      component: Signup,
      meta: {
        requiresVisitor: true
      }
    },
    {
      path: '/signout',
      name: 'signout',
      component: Signout,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/patientsReport/report/:id?',
      name: 'report',
      component: Report,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/patientsReport/reportByDate/:id?',
      name: 'reportByDate',
      component: ReportByDate,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/settings/settings/time-slot',
      name: 'settings',
      component: Settings,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        if (store.state.userInfo.isadmin) {
          next();
        } else {
          next("/")
        }
      }
    },
    {
      path: '/settings/settings/header',
      name: 'headerSettings',
      component: HeaderSettings,
      meta: {
        requiresAuth: true
      },
      beforeEnter: (to, from, next) => {
        if (store.state.userInfo.isadmin) {
          next();
        } else {
          next("/")
        }
      }
    },

  ]
})


export default router;