        'id': patient.id,
        'name': patient.name,
        'age': patient.age,
        'gender': patient.gender,
        'contact': patient.contact,
        'date': patient.patient_schedule[0].date,
        'time': patient.patient_schedule[0].time_slot.start_time + '-' + patient.patient_schedule[0].time_slot.end_time,
