<template>
  <v-container v-on:keyup.enter="login">
    <v-layout text-xs-center text-md-center>
      <v-flex xs1 sm1 md2 lg3></v-flex>
      <v-flex xs10 sm1 md8 lg6>
        <v-layout row wrap justify-center class="auth-form-header p-0">
          <h1>Sign in to Prescription</h1>
        </v-layout>
        <v-layout row wrap justify-center>
          <!-- <v-alert
                      :value="getAuthSigninResponse"
                      type="warning" 
                    >
                    {{getAuthSigninResponse}}
          </v-alert>-->
        </v-layout>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field v-model="allLogData.email" :rules="emailRules" label="E-mail" required></v-text-field>

          <v-text-field
            v-model="allLogData.password"
            type="password"
            :rules="passwordRules"
            label="Password"
            required
          ></v-text-field>

          <v-btn
            :disabled="!valid"
            v-if="allLogData.email && allLogData.password"
            color="success"
            @click.prevent="login"
          >Submit</v-btn>

          <v-btn color="error" @click="reset">Reset Form</v-btn>

          <v-btn color="warning" @click="resetValidation">Reset Validation</v-btn>
        </v-form>
      </v-flex>
      <v-flex xs1 sm1 md2 lg3></v-flex>
    </v-layout>
    <v-spacer></v-spacer>
    <v-layout row wrap justify-center class="auth-form-footer mt-4">
      <p>Not Registered?</p>
      <router-link to="/signup" style="text-decoration: none;">
        <span>Create new account</span>
      </router-link>
    </v-layout>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data: () => ({
    valid: true,
    allLogData: {
      email: "",
      password: ""
    },
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+.@.+/.test(v) || "E-mail must be valid"
    ],

    password: null,
    passwordRules: [
      v => !!v || "password is required",
      v => (v && v.length > 5) || "minimum 6 characters"
    ]
  }),

  methods: {
    login() {
      this.$store
        .dispatch("retrieveToken", {
          email: this.allLogData.email,
          password: this.allLogData.password
        })
        .then(response => {
          if (!response.data.error) {
            this.$router.push("/");
          } else {
            (this.allLogData.email = ""), (this.allLogData.password = "");
          }
        });
    },
    validate() {
      if (this.password != this.confirm_password) {
        return (this.show1 = true);
      } else {
        this.show1 = false;
      }

      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>

