<template>
  <div>
    <v-layout>
      <v-flex>
        <h2 class="grey--text">Prescription List</h2>
      </v-flex>
      <v-flex></v-flex>
    </v-layout>
    <v-spacer></v-spacer>
    <v-layout>
      <v-flex md-4></v-flex>
      <v-flex md-4>
        <v-combobox
          v-model="searchPatients.searchInput"
          :items="searchFilter"
          :search-input.sync="searchPatient"
          item-text="contact"
          item-value="name"
          item-disabled
          hint="Search by Contact Number"
          label="Search Old Patient"
          prepend-inner-icon="search"
          outline
          open-on-clear
          @input="getPrescription"
        >
          <template v-slot:item="data">
            <v-list-tile-content>
              <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
              <v-list-tile-sub-title
                v-html="'Contact: ' +data.item.contact +', Age: '+ data.item.age"
              ></v-list-tile-sub-title>
            </v-list-tile-content>
            <!-- <v-btn
              small
              fab
              dark
              color="cyan darken-2"
              slot="activator"
              @click="getPrescription(data.item)"
            >
              <v-icon>add</v-icon>
            </v-btn>-->
          </template>
        </v-combobox>
      </v-flex>
      <v-flex md4></v-flex>
    </v-layout>
    <v-spacer></v-spacer>
    <v-layout ml-4>
      <v-flex xs12 sm6 md3>
        <v-text-field label="Name" v-model="getPatientDetails.name"></v-text-field>
      </v-flex>
      <v-flex md1></v-flex>
      <v-flex xs12 sm6 md2>
        <v-text-field label="Age" v-model="getPatientDetails.age"></v-text-field>
      </v-flex>
      <v-flex md1></v-flex>
      <v-flex xs12 sm6 md2>
        <v-text-field label="Phone" v-model="getPatientDetails.contact"></v-text-field>
      </v-flex>
    </v-layout>
    <v-spacer></v-spacer>
    <v-data-table :headers="headers" :items="desserts" class="elevation-1 ma-4">
      <template v-slot:items="props">
        <td class="text--xs">{{ props.item.created_at }}</td>
        <td class="text--xs">{{ showSymptom(props.item) }}</td>
        <td class="text--xs">{{ showMedicine(props.item) }}</td>
        <td class="text--xs">{{ showTests(props.item) }}</td>
        <td class="text--xs">
          <v-tooltip top>
            <v-btn
              small
              fab
              dark
              color="cyan darken-2"
              slot="activator"
              v-show="patientInList"
              @click="editPrescription(props.item)"
            >
              <v-icon>edit</v-icon>
            </v-btn>
            <span>Edit Prescription</span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn
              small
              fab
              dark
              color="indigo"
              slot="activator"
              @click="showPrescription(props.item)"
            >
              <v-icon>remove_red_eye</v-icon>
            </v-btn>
            <span>Show Prescription</span>
          </v-tooltip>
        </td>
      </template>
    </v-data-table>
  </div>
</template>

<script>
export default {
  data: () => ({
    searchPatient: null,
    searchPatients: {
      searchInput: ""
    }
  }),
  created() {
    this.fetchData();
  },
  watch: {
    searchPatient(val) {
      this.retrievePatientbySearch(val);
    }
  },
  computed: {
    headers() {
      return this.$store.state.prescriptionHeaders;
    },
    getPatientDetails() {
      return this.$store.getters.getPrescriptionHeadBySearch;
    },
    desserts() {
      return this.$store.getters.getPrescriptionBySearch;
    },
    patientInfo() {
      return this.$store.getters.getPatientsBySearch;
    },
    searchFilter() {
      return this.patientInfo.filter(getPatient => {
        return (
          getPatient.name.toLowerCase().match(this.searchPatient) ||
          getPatient.contact.match(this.searchPatient)
        );
      });
    },
    patientInList() {
      return this.$store.state.patientInList;
    },
    patientScheduleForEdit() {
      return this.$store.getters.patientScheduleForEdit;
    }
  },

  methods: {
    fetchData() {
      if (typeof this.$route.params.id != "undefined") {
        this.$store.dispatch("getPrescriptionReport", {
          id: this.$route.params.id
        });
      }
    },
    showTests(item) {
      var twoTests = item.tests.slice(0, 2);

      var testsInfo = "";

      twoTests.map(test => {
        testsInfo += test.name + ", ";
      });

      return testsInfo;
    },
    showSymptom(item) {
      var twoSymptom = item.prescription_symptom.slice(0, 2);

      var symptomInfo = "";

      twoSymptom.map(symptom => {
        symptomInfo += symptom.symptoms + ", ";
      });

      return symptomInfo;
    },
    showMedicine(item) {
      var twoMedicines = item.medicines.slice(0, 2);

      var medicineInfo = "";

      twoMedicines.map(medicine => {
        medicineInfo += medicine.name + ", ";
      });

      return medicineInfo;
    },
    retrievePatientbySearch(searchBy) {
      var searchInfo = {
        searchInput: searchBy
      };
      this.$store.dispatch("retrievePatientbySearch", searchInfo);
    },
    getPrescription(item) {
      var abc = this.patientScheduleForEdit;
      // this.$router.push("/patientsReport/report/" + item.id);
      this.$store.dispatch("getPrescriptionReport", item);
    },
    showPrescription(item) {
      this.$router.push("/prescriptions/" + item.id);
    },
    editPrescription(item) {
      var patientSchedule = this.patientScheduleForEdit;
      this.$store
        .dispatch("getDetailsForEditPrescription", item.id)
        .then(response => {
          this.$router.push(
            "/prescriptions/patient/" +
              patientSchedule.patient_id +
              "/" +
              patientSchedule.id +
              "/" +
              patientSchedule.date +
              "/" +
              item.id
          );
        });
    }
  }
};
</script>

